* {
    box-sizing: border-size;
}

body {
    margin: 0;
    padding: 0;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    background-color: black;
    height: 100%;
}

html {
    color: white;
}

html,
body,
#theater,

.loader{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    
}

.movies {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}